.first-screen .white {
  color: #ffffff;
}

.first-screen .black {
  color: #000000;
}

.first-screen {
  min-height: 640px;

  @media screen and (min-width: 320px) and (max-width: 767px) {
    background-image: none !important;
  }
  @media screen and (min-width: 768px) {
  }
  @media screen and (min-width: 1280px) {
  }
  .first-screen-mobile {
    min-height: 600px;
    background-position: 100% 20%;
    background-repeat: no-repeat;
    background-size: contain;

    @media screen and (min-width: 320px) and (max-width: 767px) {
    }
    @media screen and (min-width: 768px) {
      background-image: none !important;
    }
    @media screen and (min-width: 1280px) {
      background-image: none !important;
    }
  }
  h1 {
    font-family: 'Ubuntu';
    padding: 50px 55% 30px 0;
    font-size: 50px;
    line-height: 1.1;
    font-weight: 500;
    color: $main-text-color;
    clear: both;

    @media screen and (min-width: 320px) and (max-width: 767px) {
      font-size: 30px;
      line-height: 1.3;
      text-align: center;
      font-weight: 300;
      padding: 20px 0;
    }
    @media screen and (min-width: 768px) {
    }
    @media screen and (min-width: 1280px) {
    }
  }

  h2 {
    font-family: 'Ubuntu';
    padding: 0 48% 35px 0;
    font-size: 24px;
    line-height: 1.4;
    font-weight: 300;
    color: $main-text-color;
    @media screen and (min-width: 320px) and (max-width: 767px) {
      font-size: 16px;
      line-height: 1.3;
      text-align: center;
      font-weight: 300;
      padding: 40px 0 50px 0;
    }
    @media screen and (min-width: 768px) {
    }
    @media screen and (min-width: 1280px) {
    }
  }

  .first-screen-button {
    width: 450px;
    height: auto;
    display: inline-block;
    padding: 14px 10px;
    //margin: 0 0 45px 0;
    font-weight: 400;
    font-size: 26px;
    text-align: center;
    text-decoration: none;
    border: 1px solid $button-text-color;
    background-color: $button-text-color;
    color: $button-bgc;
    line-height: 1.55;
    font-weight: 600;
    border-width: 1px;
    border-radius: 10px;
    border-color: transparent;
    border-style: solid;
    transition: all 0.2s ease-in-out, color 0.2s ease-in-out;
    &:hover {
      box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px,
        rgba(0, 0, 0, 0.23) 0px 3px 6px;
    }
    @media screen and (min-width: 320px) and (max-width: 767px) {
      display: inline-block;
      width: 100%;
      font-size: 21px;
      padding: 20px 10px;
      margin: 0;
      line-height: 1.2;
    }
    @media screen and (min-width: 768px) {
    }
    @media screen and (min-width: 1280px) {
    }
  }

  .first-screen-icons {
    display: flex;
    flex-wrap: nowrap;
    flex-direction: column;
    margin: 0;
    padding: 0;
    color: #ffffff;
    list-style-type: none;
    margin: 40px 0;
    @media screen and (min-width: 320px) and (max-width: 767px) {
      display: inline-block;
      width: 100%;
      padding: 20px 10px;
      margin: 0;
    }
    @media screen and (min-width: 768px) {
    }
    @media screen and (min-width: 1280px) {
    }
    li {
      display: flex;
      flex-wrap: nowrap;
      align-items: center;
    }

    li:nth-child(2) {
      margin-top: 25px;
      @media screen and (min-width: 320px) and (max-width: 767px) {
        margin-top: 15px;
      }
      @media screen and (min-width: 768px) {
      }
      @media screen and (min-width: 1280px) {
      }
    }

    li span {
      display: inline-block;
      padding: 20px;
      vertical-align: middle;
      font-weight: 300;
      font-size: 22px;

      @media screen and (min-width: 320px) and (max-width: 767px) {
        font-size: 16px;
      }
      @media screen and (min-width: 768px) {
      }
      @media screen and (min-width: 1280px) {
      }
    }
  }
}
