body {
  background: #fff;
  font-family: Ubuntu, sans-serif;
  font-style: normal;
  font-weight: 500;
}

.noscroll {
  position: fixed;
  overflow: hidden;
}

img {
  max-width: 100%;
  display: block;
}

body, p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.link {
  text-decoration: none;
}

.hidden {
  width: 1px;
  height: 1px;
  clip: rect(0, 0, 0, 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
}

.container {
  width: 1280px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

