.header-menu ul {
  width: 1280px;
  height: auto;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0 auto;
  padding: 20px 0;
  display: flex;
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .header-menu ul {
    display: none;
  }
}

.header-menu ul, .header-menu ul li {
  list-style-type: none;
}

.header-menu ul li {
  margin-right: 40px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: relative;
}

.header-menu ul li:hover, .header-menu ul li:focus {
  transform: scale(1.3);
}

.header-menu ul li:not(:last-child) {
  margin-right: 40px;
}

.header-menu ul li a {
  color: #0a0a0a;
  font-size: 18px;
  font-weight: 400;
  text-decoration: none;
}

.header-menu ul li ul li a {
  font-size: 14px;
}

.header-menu ul li ul {
  max-width: 250px;
  visibility: hidden;
  opacity: 0;
  background-color: #fff;
  flex-wrap: wrap;
  transition: opacity .2s linear;
  display: flex;
  position: absolute;
  top: 20px;
  left: -20px;
}

.header-menu ul li:hover ul {
  visibility: visible;
  opacity: 1;
  transition: opacity .2s linear;
}

.header-menu ul li ul li {
  min-width: 250px;
  padding: 5px 20px;
  transition: none;
}

.header-menu ul li ul li:hover, .header-menu ul li ul li:focus {
  transform: scale(1);
}

.header-menu ul li ul li a:hover {
  color: #ffd952;
}

.header-menu ul li ul li .header-menu ul li ul li a {
  font-size: 14px;
}

.header-menu ul li a .fa-angle-down {
  vertical-align: middle;
  transition: all .5s ease-in-out;
}

.header-menu ul li:hover a .fa-angle-down {
  transition: all .5s ease-in-out;
  transform: rotate(-90deg);
}

.three, .mobile-menu {
  display: none;
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .three, .mobile-menu {
    display: block;
  }
}

.visible {
  display: block !important;
}

.mobile-menu {
  height: 100vh;
  width: 100%;
  background-color: #111;
  transition: all .5s;
  display: none;
  position: absolute;
  top: 0;
  left: 0;
}

.mobile-menu .container-mobile .menu-container {
  width: 250px;
  height: 100vh;
  z-index: 99;
  background-color: #e17992;
  flex-direction: column;
  padding: 60px 0;
  transition: transform .35s cubic-bezier(.8, .4, .3, .9);
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
}

.mobile-menu ul {
  padding: 50px 30px;
}

.mobile-menu ul li {
  margin: 15px 0;
  list-style-type: none;
  position: relative;
}

.mobile-menu ul li a {
  color: #efefef;
  font-size: 22px;
  text-decoration: none;
}

.mobile-menu ul li ul {
  margin: 0;
  padding: 0 30px;
}

.mobile-menu ul li div.click {
  color: #fff;
  font-size: 20px;
  position: absolute;
  top: 0;
  left: 220px;
}

.container-mobile ul ul li a {
  font-size: 18px;
}

.mobile-menu ul ul {
  display: none;
}

.hamburger {
  z-index: 1001;
  background-color: #111;
  padding: 5px;
  position: fixed;
  top: 35px;
  right: 20px;
}

.hamburger:hover {
  cursor: pointer;
}

.hamburger .line {
  width: 30px;
  height: 5px;
  -o-transition: all .3s ease-in-out;
  background-color: #fff;
  margin: 5px;
  transition: all .3s ease-in-out;
  display: block;
}

#hamburger-1.is-active .line:nth-child(2) {
  opacity: 0;
}

#hamburger-1.is-active .line:nth-child(1) {
  transform: translateY(10px)rotate(45deg);
}

#hamburger-1.is-active .line:nth-child(3) {
  transform: translateY(-10px)rotate(-45deg);
}

